<h3 className="">{"Form Validation"}</h3>
{"\n"}
<Example>
    <mwc-button
        raised
        onClick={({ target }) =>
            (target as HTMLElement).nextElementSibling.show()
        }
    >
        Show Dialog
    </mwc-button>
    <mwc-dialog id="dialog" heading="Form Validation">
        <p>This dialog can validate user input before closing.</p>
        <mwc-textfield
            id="text-field"
            minLength={3}
            maxLength={64}
            placeholder="First name"
            required
        />
        <mwc-button 
            id="primary-action-button" 
            slot="primaryAction" 
            onClick={({ target }) => {
                const textField = (target as HTMLElement).previousElementSibling
                // validate, possible asynchronous such as a server response
                if (textField.checkValidity())
                    (target as HTMLElement).closest('mwc-dialog').close();
                else
                    textField.reportValidity();
            }}
        >
            Confirm
        </mwc-button>
        <mwc-button slot="secondaryAction" dialogAction="close">
            Cancel
        </mwc-button>
    </mwc-dialog>
</Example>
