<h2 className="">{"API"}</h2>
{"\n"}
<h3 className="">{"Slots"}</h3>
{"\n"}
<table className="">{"\n"}
<thead className="">{"\n"}
<tr className="">{"\n"}
<th align="null" className="">{"Name"}</th>
{"\n"}
<th align="null" className="">{"Description"}</th>
{"\n"}</tr>
{"\n"}</thead>
{"\n"}
<tbody className="">{"\n"}
<tr className="">{"\n"}
<td align="null" className=""><code className="">{"primaryAction"}</code></td>
{"\n"}
<td align="null" className="">{"A focusable and clickable target. Typically a button such as "}
<code className="">{"<mwc-button>"}</code>
{". Placed on the bottom right of the dialog (LTR) and above the secondary action when stacked. Automatically clicked when "}
<code className="">{"Enter"}</code>
{" key is pressed in the dialog."}</td>
{"\n"}</tr>
{"\n"}
<tr className="">{"\n"}
<td align="null" className=""><code className="">{"secondaryAction"}</code></td>
{"\n"}
<td align="null" className="">{"A focusable and clickable target. Typically a button such as "}
<code className="">{"<mwc-button>"}</code>
{". Placed immediately to the left of the "}
<code className="">{"primaryAction"}</code>
{" (LTR) or below when stacked."}</td>
{"\n"}</tr>
{"\n"}
<tr className="">{"\n"}
<td align="null" className=""><em className="">{"default"}</em></td>
{"\n"}
<td align="null" className="">{"Content to display in the dialog's content area."}</td>
{"\n"}</tr>
{"\n"}</tbody>
{"\n"}</table>
