<h3 className="">{"Actions"}</h3>
{"\n"}
<p className="">{"Actions close the dialog on click. You can define an action by slotting an\nelement with the "}
<code className="">{"dialogAction=\"...\""}</code>
{" string attribute. The name of the\nattribute can be customized by the\n"}
<a href="#propertiesattributes" className=""><code className="">{"actionAttribute"}</code>
{" property"}</a>
{". When a clickable element\nwith the "}
<code className="">{"dialogAction"}</code>
{" attribute is clicked, "}
<code className="">{"mwc-dialog"}</code>
{" will get the value\nof the attribute and fire the "}
<code className="">{"closing"}</code>
{" and subsequent "}
<code className="">{"closed"}</code>
{" events with a\ndetail of "}
<code className="">{"{action: <clickedElement.getAttribute('dialogAction')>}"}</code>
{"."}</p>
{"\n"}
<p className="">{"For example:"}</p>
{"\n"}
<Example>
    <mwc-button
        raised
        onClick={({ target }) =>
            (target as HTMLElement).nextElementSibling.show()
        }
    >
        Show Dialog
    </mwc-button>
    <mwc-dialog>
        <div>
            <div>
                This is my content. Here is an actionable button:
                <button dialogAction="contentButton">button 1</button>
            </div>
            <div>
                This is my content. Here is a diabled actionable button:
                <button disabled dialogAction="disabledContentButton">
                    button 2
                </button>
            </div>
        </div>
        <mwc-button slot="primaryAction" dialogAction="ok">
            ok
        </mwc-button>
        <mwc-button slot="secondaryAction">cancel</mwc-button>
    </mwc-dialog>
</Example>
