{"\n"}
<h3 className="">{"Focus"}</h3>
{"\n"}
<p className="">{"Initial focus can be set on an element with the "}
<code className="">{"dialogInitialFocus"}</code>
{" boolean\nattribute (configurable via the "}
<a href="#propertiesattributes" className=""><code className="">{"initialFocusAttribute"}</code>
{" property"}</a>
{")."}</p>
{"\n"}
<p className="">{"For example:"}</p>
{"\n"}
<Example>
    <mwc-button
        raised
        onClick={({ target }) =>
            (target as HTMLElement).nextElementSibling.show()
        }
    >
        Show Dialog
    </mwc-button>
    <mwc-dialog heading="Initial Focus">
        <div>
            In this example we set "dialogInitialFocus" on the mwc-textfield.
            When this dialog opens, it is auto-focused.
        </div>
        <mwc-textfield
            label="i am auto-focused"
            dialogInitialFocus
        />
        <mwc-button slot="primaryAction" dialogAction="close">
            Primary
        </mwc-button>
        <mwc-button slot="secondaryAction" dialogAction="close">
            Secondary
        </mwc-button>
    </mwc-dialog>
</Example>
